import { CurrencyAmount, Token, Trade } from '@pancakeswap-libs/sdk'
import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { CardBody, IconButton } from '@pancakeswap-libs/uikit'
import { Button } from 'react-bootstrap'
import { AutoColumn } from 'components/Column'
import CurrencyInputPanel from 'components/CurrencyInputPanel'
import { AutoRow, RowBetween } from 'components/Row'
import { ArrowWrapper, BottomGrouping, SwapCallbackError } from 'components/swap/styleds'
import TokenWarningModal from 'components/TokenWarningModal'
import SyrupWarningModal from 'components/SyrupWarningModal'
import SafeMoonWarningModal from 'components/SafeMoonWarningModal'
import ProgressSteps from 'components/ProgressSteps'
import { IoSwapVertical } from 'react-icons/io5'

import { useActiveWeb3React } from 'hooks'
import { useCurrency } from 'hooks/Tokens'
import { ApprovalState, useApproveOrderCallback } from 'hooks/useApproveCallback'
import { useOpenOrderCallback } from 'hooks/useOpenOrderCallback'
import useWrapCallback, { WrapType } from 'hooks/useWrapCallback'
import { Field } from 'state/swap/actions'
import { useDefaultsFromURL, useDerivedSwapInfo, useSwapActionHandlers, useSwapState } from 'state/swap/hooks'
import { useExpertModeManager, useUserDeadline } from 'state/user/hooks'
import { maxAmountSpend } from 'utils/maxAmountSpend'
import Loader from 'components/Loader'
import PageHeader from 'components/PageHeader'
import ConnectWalletButton from 'components/ConnectWalletButton'
import defaultTokenJson from '../../constants/token/pancakeswap.json'

export default function LimitTradeBox() {
  const loadedUrlParams = useDefaultsFromURL()

  // token warning stuff
  // TODO unknown tokens take a while to show warning modal
  const [loadedInputCurrency, loadedOutputCurrency] = [
    useCurrency(loadedUrlParams?.inputCurrencyId),
    useCurrency(loadedUrlParams?.outputCurrencyId),
  ]
  const [dismissTokenWarning, setDismissTokenWarning] = useState<boolean>(false)
  const [transactionWarning, setTransactionWarning] = useState<{
    selectedToken: string | null
    purchaseType: string | null
  }>({
    selectedToken: null,
    purchaseType: null,
  })
  const urlLoadedTokens: Token[] = useMemo(
    () => [loadedInputCurrency, loadedOutputCurrency]?.filter((c): c is Token => c instanceof Token) ?? [],
    [loadedInputCurrency, loadedOutputCurrency]
  )
  const unknownTokens = urlLoadedTokens
    .filter(token => !defaultTokenJson.tokens.some(known => known.address.toLowerCase() === token.address.toLowerCase()))
  const inputTokenLoaded = !loadedUrlParams?.inputCurrencyId || loadedUrlParams?.inputCurrencyId === 'BNB' || !!loadedInputCurrency
  const outputTokenLoaded = !loadedUrlParams?.outputCurrencyId || loadedUrlParams?.outputCurrencyId === 'BNB' || !!loadedOutputCurrency
  const handleConfirmTokenWarning = useCallback(() => {
    setDismissTokenWarning(true)
  }, [])

  const handleConfirmWarning = () => {
    setTransactionWarning({
      selectedToken: null,
      purchaseType: null,
    })
  }

  const { account } = useActiveWeb3React()

  const [isExpertMode] = useExpertModeManager()

  // get custom setting values for user
  const [deadline] = useUserDeadline()

  // swap state
  const { independentField, inputValue, independentValue } = useSwapState()
  const {
    order,
    priceTrade,
    currencyBalances,
    inputAmount,
    independentAmount,
    currencies,
    inputError: swapInputError
  } = useDerivedSwapInfo()
  const { wrapType, execute: onWrap, inputError: wrapInputError } = useWrapCallback(
    currencies[Field.INPUT],
    currencies[Field.OUTPUT],
    independentValue,
  )
  const showWrap: boolean = wrapType !== WrapType.NOT_APPLICABLE
  const trade = showWrap || !priceTrade ? undefined : priceTrade
  const independentOrTradePrice = independentAmount ?? trade?.executionPrice

  const parsedAmounts = showWrap
    ? {
        [Field.INPUT]: inputAmount,
        [Field.OUTPUT]: inputAmount,
      }
    : {
        [Field.INPUT]: inputAmount,
        [Field.PRICE]: independentField === Field.PRICE
          ? independentOrTradePrice
          : inputAmount && independentAmount?.divide(inputAmount),
        [Field.OUTPUT]: independentField === Field.OUTPUT
          ? independentAmount
          : independentOrTradePrice && inputAmount?.multiply(independentOrTradePrice),
      }

  const { onSwitchTokens, onCurrencySelection, onUserInput } = useSwapActionHandlers()
  const isValid = !swapInputError
  const dependentField: Field = independentField === Field.PRICE ? Field.OUTPUT : Field.PRICE

  const handleTypeInput = useCallback(
    (value: string) => {
      onUserInput(Field.INPUT, value)
    },
    [onUserInput]
  )
  const handleTypePrice = useCallback(
    (value: string) => {
      onUserInput(Field.PRICE, value)
    },
    [onUserInput]
  )
  const handleTypeOutput = useCallback(
    (value: string) => {
      onUserInput(Field.OUTPUT, value)
    },
    [onUserInput]
  )

  // modal and loading
  const [{ swapErrorMessage }, setSwapState] = useState<{
    showConfirm: boolean
    tradeToConfirm: Trade | undefined
    attemptingTxn: boolean
    swapErrorMessage: string | undefined
    txHash: string | undefined
  }>({
    showConfirm: false,
    tradeToConfirm: undefined,
    attemptingTxn: false,
    swapErrorMessage: undefined,
    txHash: undefined,
  })

  const formattedAmounts = {
    [Field.INPUT]: inputValue,
    [independentField]: independentValue || (parsedAmounts[independentField]?.toSignificant(6) ?? ''),
    [dependentField]: showWrap
      ? (parsedAmounts[independentField] as CurrencyAmount)?.toExact() ?? ''
      : parsedAmounts[dependentField]?.toSignificant(6) ?? ''
  }

  // check whether the user has approved the router on the input token
  const [approval, approveCallback] = useApproveOrderCallback(inputAmount)

  // check if user has gone through approval process, used to show two step buttons, reset on token change
  const [approvalSubmitted, setApprovalSubmitted] = useState<boolean>(false)

  // mark when a user has submitted an approval, reset onTokenSelection for input field
  useEffect(() => {
    if (approval === ApprovalState.PENDING) {
      setApprovalSubmitted(true)
    }
  }, [approval, approvalSubmitted])

  const maxAmountInput: CurrencyAmount | undefined = maxAmountSpend(currencyBalances[Field.INPUT])
  const atMaxAmountInput = Boolean(maxAmountInput && parsedAmounts[Field.INPUT]?.equalTo(maxAmountInput))

  // the callback to execute the swap
  const { callback: openOrderCallback, error: swapCallbackError } = useOpenOrderCallback(
    order,
    deadline
  )

  const handleOpenOrder = useCallback(async () => {
    if (!openOrderCallback) {
      return
    }
    setSwapState((prevState) => ({ ...prevState, attemptingTxn: true, swapErrorMessage: undefined, txHash: undefined }))
    try {
      const hash = await openOrderCallback()
      setSwapState((prevState) => ({
        ...prevState,
        attemptingTxn: false,
        swapErrorMessage: undefined,
        txHash: hash,
      }))
    } catch (error) {
      setSwapState((prevState) => ({
        ...prevState,
        attemptingTxn: false,
        swapErrorMessage: error.message,
        txHash: undefined,
      }))
    }
  }, [openOrderCallback, setSwapState])

  // warnings on slippage
  const priceImpactSeverity = 0 // warningSeverity(priceImpactWithoutFee)

  // show approve flow when: no error on inputs, not approved or pending, or approved in current session
  // never show if price impact is above threshold in non expert mode
  const showApproveFlow =
    !swapInputError &&
    (approval === ApprovalState.NOT_APPROVED ||
      approval === ApprovalState.PENDING ||
      (approvalSubmitted && approval === ApprovalState.APPROVED)) &&
    !(priceImpactSeverity > 3 && !isExpertMode)

  // This will check to see if the user has selected Syrup or SafeMoon to either buy or sell.
  // If so, they will be alerted with a warning message.
  const checkForWarning = useCallback(
    (selected: string, purchaseType: string) => {
      if (['SYRUP', 'SAFEMOON'].includes(selected)) {
        setTransactionWarning({
          selectedToken: selected,
          purchaseType,
        })
      }
    },
    [setTransactionWarning]
  )

  const handleInputSelect = useCallback(
    (inputCurrency) => {
      setApprovalSubmitted(false) // reset 2 step UI for approvals
      onCurrencySelection(Field.INPUT, inputCurrency)
      if (inputCurrency.symbol === 'SYRUP') {
        checkForWarning(inputCurrency.symbol, 'Selling')
      }
      if (inputCurrency.symbol === 'SAFEMOON') {
        checkForWarning(inputCurrency.symbol, 'Selling')
      }
    },
    [onCurrencySelection, setApprovalSubmitted, checkForWarning]
  )

  const handleMaxInput = useCallback(() => {
    if (maxAmountInput) {
      onUserInput(Field.INPUT, maxAmountInput.toExact())
    }
  }, [maxAmountInput, onUserInput])

  const handleOutputSelect = useCallback(
    (outputCurrency) => {
      onCurrencySelection(Field.OUTPUT, outputCurrency)
      if (outputCurrency.symbol === 'SYRUP') {
        checkForWarning(outputCurrency.symbol, 'Buying')
      }
      if (outputCurrency.symbol === 'SAFEMOON') {
        checkForWarning(outputCurrency.symbol, 'Buying')
      }
    },
    [onCurrencySelection, checkForWarning]
  )

  return (
    <>
      <TokenWarningModal
        isOpen={unknownTokens.length > 0 && !dismissTokenWarning}
        tokens={unknownTokens}
        onConfirm={handleConfirmTokenWarning}
      />
      <SyrupWarningModal
        isOpen={transactionWarning.selectedToken === 'SYRUP'}
        transactionType={transactionWarning.purchaseType}
        onConfirm={handleConfirmWarning}
      />
      <SafeMoonWarningModal isOpen={transactionWarning.selectedToken === 'SAFEMOON'} onConfirm={handleConfirmWarning} />
      <PageHeader
        title="Place Order"
        description="Market makers have no fees"
      />
      <CardBody>
        <AutoColumn gap="md">
          <CurrencyInputPanel
            label="You Pay"
            value={formattedAmounts[Field.INPUT]}
            showMaxButton={!atMaxAmountInput}
            currency={currencies[Field.INPUT]}
            currencyLoading={!inputTokenLoaded}
            onUserInput={handleTypeInput}
            onMax={handleMaxInput}
            onCurrencySelect={handleInputSelect}
            otherCurrency={currencies[Field.OUTPUT]}
            id="swap-currency-input"
          />

          {
            !showWrap && (
              <CurrencyInputPanel
                value={formattedAmounts[Field.PRICE]}
                onUserInput={handleTypePrice}
                label={
                  currencies[Field.INPUT]?.symbol
                    ? `Price per ${currencies[Field.INPUT]?.symbol}`
                    : 'Price'
                }
                showMaxButton={false}
                disableCurrencySelect
                hideBalance
                currency={currencies[Field.OUTPUT]}
                onCurrencySelect={handleOutputSelect}
                otherCurrency={currencies[Field.INPUT]}
                id="price"
          />
            )
          }

          <AutoColumn justify="space-between">
            <AutoRow justify={isExpertMode ? 'space-between' : 'center'} style={{ padding: '0 1rem' }}>
              <ArrowWrapper clickable>
                <IconButton
                  variant="tertiary"
                  onClick={() => {
                    setApprovalSubmitted(false) // reset 2 step UI for approvals
                    onSwitchTokens()
                  }}
                  style={{ borderRadius: '50%' }}
                  scale="md"
                >
                  <IoSwapVertical size={25} />
                </IconButton>
              </ArrowWrapper>
            </AutoRow>
          </AutoColumn>

          <CurrencyInputPanel
            value={formattedAmounts[Field.OUTPUT]}
            onUserInput={handleTypeOutput}
            label="You Receive"
            showMaxButton={false}
            currency={currencies[Field.OUTPUT]}
            currencyLoading={!outputTokenLoaded}
            onCurrencySelect={handleOutputSelect}
            otherCurrency={currencies[Field.INPUT]}
            id="swap-currency-output"
          />

          {/* {recipient !== null && !showWrap ? (
            <>
              <AutoRow justify="space-between" style={{ padding: '0 1rem' }}>
                <ArrowWrapper clickable={false}>
                  <ArrowDown size="16" color={theme.colors.textSubtle} />
                </ArrowWrapper>
                <LinkStyledButton id="remove-recipient-button" onClick={() => onChangeRecipient(null)}>
                  - Remove send
                </LinkStyledButton>
              </AutoRow>
              <AddressInputPanel id="recipient" value={recipient} onChange={onChangeRecipient} />
            </>
          ) : null} */}

        </AutoColumn>
        <BottomGrouping>
          {!account ? (
            <ConnectWalletButton block />
          ) : showWrap ? (
            <Button disabled={Boolean(wrapInputError)} onClick={onWrap} block>
              {wrapInputError ??
                (wrapType === WrapType.WRAP ? 'Wrap' : wrapType === WrapType.UNWRAP ? 'Unwrap' : null)}
            </Button>
          ) : showApproveFlow ? (
            <RowBetween>
              <Button
                onClick={approveCallback}
                disabled={approval !== ApprovalState.NOT_APPROVED || approvalSubmitted}
                style={{ width: '48%' }}
                variant={approval === ApprovalState.APPROVED ? 'success' : 'primary'}
              >
                {approval === ApprovalState.PENDING ? (
                  <AutoRow gap="6px" justify="center">
                    Approving <Loader stroke="white" />
                  </AutoRow>
                ) : approvalSubmitted && approval === ApprovalState.APPROVED ? (
                  'Approved'
                ) : (
                  `Approve ${currencies[Field.INPUT]?.symbol}`
                )}
              </Button>
              <Button
                onClick={() => handleOpenOrder()}
                style={{ width: '48%' }}
                id="open-order-button"
                disabled={
                  !isValid || approval !== ApprovalState.APPROVED || (priceImpactSeverity > 3 && !isExpertMode)
                }
                variant={isValid && priceImpactSeverity > 2 ? 'danger' : 'primary'}
              >
                {priceImpactSeverity > 3 && !isExpertMode
                  ? `Price Impact High`
                  : `Open Limit Order${priceImpactSeverity > 2 ? ' Anyway' : ''}`}
              </Button>
            </RowBetween>
          ) : (
            <Button
              onClick={() => handleOpenOrder()}
              id="open-order-button"
              disabled={!isValid || (priceImpactSeverity > 3 && !isExpertMode) || !!swapCallbackError}
              variant={isValid && priceImpactSeverity > 2 && !swapCallbackError ? 'danger' : 'primary'}
              block
            >
              {swapInputError ||
                (priceImpactSeverity > 3 && !isExpertMode
                  ? `Price Impact Too High`
                  : `Open Limit Order${priceImpactSeverity > 2 ? ' Anyway' : ''}`)}
            </Button>
          )}
          {showApproveFlow && <ProgressSteps steps={[approval === ApprovalState.APPROVED]} />}
          {isExpertMode && swapErrorMessage ? <SwapCallbackError error={swapErrorMessage} /> : null}
        </BottomGrouping>
      </CardBody>
    </>
  )
}
