import { Currency, CurrencyAmount } from '@pancakeswap-libs/sdk'
import { useActiveWeb3React } from 'hooks'
import { useCallback, useEffect, useState } from 'react'
import { useTransactionAdder } from 'state/transactions/hooks'
import getCurrencyAddress from 'utils/getCurrencyAddress'

const ENDPOINT = 'https://api.1inch.exchange/v3.0/56'
const ONEINCH_BNB_ADDRESS = '0xeeeeeeeeeeeeeeeeeeeeeeeeeeeeeeeeeeeeeeee'

interface OneInchToken {
  symbol: string
  name: string
  address: string
  decimals: number
  logoURI: string
}

interface OneInchProtocol {
  name: string
  part: number
  fromTokenAddress: string
  toTokenAddress: string
}

interface OneInchTx {
  from?: string
  to: string
  data: string // call data
  value: string // amount of eth/bnb (in wei) will be sent to the contract address
  gasPrice: string
  gas?: number // rough estimate, increase gas value by ~25%
}

// https://docs.1inch.io/api/quote-swap#quote
interface OneInchQuoteParams {
  baseCurrency: Currency
  quoteCurrency: Currency
  amount: CurrencyAmount
}

interface OneInchQuoteResponse {
  fromToken: OneInchToken
  toToken: OneInchToken
  toTokenAmount: string
  fromTokenAmount: string
  protocols: OneInchProtocol[]
  estimatedGas: number
}

// https://docs.1inch.io/api/quote-swap#swap
interface OneInchSwapParams {
  baseCurrency: Currency
  quoteCurrency: Currency
  amount: CurrencyAmount
  fromAddress: string
  slippage: number
}

interface OneInchSwapResponse {
  fromToken: OneInchToken
  toToken: OneInchToken
  toTokenAmount: string
  fromTokenAmount: string
  protocols: OneInchProtocol[]
  tx: OneInchTx
}

