export function useOneInchSwap (opts?: OneInchSwapParams) {
  const [data, setData] = useState<OneInchSwapResponse | undefined>()
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | undefined>()
  const [needsAllowance, setNeedsAllowance] = useState(false)

  const fromTokenAddress = getCurrencyAddress(opts?.baseCurrency, ONEINCH_BNB_ADDRESS)
  const toTokenAddress = getCurrencyAddress(opts?.quoteCurrency, ONEINCH_BNB_ADDRESS)
  const amount = opts?.amount?.raw?.toString(10)
  const fromAddress = opts?.fromAddress
  const slippage = opts?.slippage?.toString()

  const requestCallback = useCallback(async (): Promise<OneInchSwapResponse | undefined> => {
    if (!fromTokenAddress || !toTokenAddress || !amount || !fromAddress || !slippage) {
      return undefined
    }

    const url = new URL(`${ENDPOINT}/swap`)
    url.search = new URLSearchParams({
      fromTokenAddress,
      toTokenAddress,
      amount,
      fromAddress,
      slippage
    }).toString()

    const quoteUrl = new URL(`${ENDPOINT}/quote`)
    quoteUrl.search = new URLSearchParams({
      fromTokenAddress,
      toTokenAddress,
      amount
    }).toString()

    try {
      setLoading(true)
      setError(undefined)
      const res = await fetch(url.toString())
      if (res.status <= 399) {
        const json = await res.json()
        setData(json)
        return json
      }
      const errorMessage = (await res.json())?.message

      if (!errorMessage?.includes('Not enough allowance')) {
        setError(errorMessage)
        return undefined
      }
      
      // Not enough allowance for the swap, fetch price from quote endpoint
      setNeedsAllowance(true)
      const quoteRes = await fetch(quoteUrl.toString())
      if (quoteRes.status <= 399) {
        const json = await quoteRes.json()
        setData(json)
        return json
      }
      setError((await quoteRes.json())?.message)
    } catch (e) {
      setError(e?.message)
    } finally {
      setLoading(false)
    }

    return undefined
  }, [fromTokenAddress, toTokenAddress, amount, fromAddress, slippage])

  useEffect(() => {
    requestCallback()
  }, [requestCallback])

  return {
    data,
    needsAllowance,
    loading,
    error,
    refetch: requestCallback,
  }
}

export function useOneInchApprove () {
  const [data, setData] = useState<OneInchSwapResponse | undefined>()
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | undefined>()

  const approveCallback = useCallback(
    async (approveCurrency: Currency): Promise<OneInchTx | undefined> =>
  {
    const tokenAddress = getCurrencyAddress(approveCurrency, ONEINCH_BNB_ADDRESS)
    if (!tokenAddress) {
      return undefined
    }

    const url = new URL(`${ENDPOINT}/approve/calldata`)
    url.search = new URLSearchParams({
      tokenAddress,
      infinity: 'true'
    }).toString()

    try {
      setLoading(true)
      setError(undefined)
      const res = await fetch(url.toString())
      if (res.status <= 399) {
        const json = await res.json()
        setData(json)
        return json
      }
      setError((await res.json())?.message)
    } catch (e) {
      setError(e?.message)
    } finally {
      setLoading(false)
    }

    return undefined
  }, [])

  return {
    data,
    loading,
    error,
    approveCallback
  }
}

export function useSendOneInchTxCallback () {
  const { library } = useActiveWeb3React()
  const [loading, setLoading] = useState(false)
  const [txHash, setTxHash] = useState<string | undefined>()
  const [error, setError] = useState('')
  const addTransaction = useTransactionAdder()

  const sendTx = useCallback(async (tx: OneInchTx, txSummary: string) => {
    setLoading(true)
    setError('')
    try {
      const txResponse = await library?.getUncheckedSigner()?.sendTransaction({
        from: tx.from,
        to: tx.to,
        data: tx.data,
        value: `0x${BigInt(tx.value).toString(16)}`,
        // gasPrice: `0x${BigInt(tx.gasPrice).toString(16)}`,
      })

      setTxHash(txResponse?.hash)

      if (txResponse) {
        addTransaction(txResponse, {
          summary: txSummary
        })
      }
    } catch (e) {
      setError(e?.message)
    } finally {
      setLoading(false)
    }
  }, [library, addTransaction])

  return {
    sendTransactionCallback: sendTx,
    txHash,
    loading,
    error
  }
}
