import { CurrencyAmount, Price, Token } from '@pancakeswap-libs/sdk'
import React, { useCallback, useContext, useMemo, useState } from 'react'
import { CardBody, IconButton, Text } from '@pancakeswap-libs/uikit'
import { Button, ButtonProps, Spinner } from 'react-bootstrap'
import { ThemeContext } from 'styled-components'
import Card from 'components/Card'
import { AutoColumn } from 'components/Column'
import ConfirmSwapModal from 'components/swap/ConfirmSwapModal'
import CurrencyInputPanel from 'components/CurrencyInputPanel'
import { AutoRow, RowBetween } from 'components/Row'
import { ArrowWrapper, BottomGrouping, SwapCallbackError, Wrapper } from 'components/swap/styleds'
import TradePrice from 'components/swap/TradePrice'
import TokenWarningModal from 'components/TokenWarningModal'
import SyrupWarningModal from 'components/SyrupWarningModal'
import SafeMoonWarningModal from 'components/SafeMoonWarningModal'
import ProgressSteps from 'components/ProgressSteps'
import { IoSwapVertical } from 'react-icons/io5'

import { INITIAL_ALLOWED_SLIPPAGE } from 'constants/index'
import { useActiveWeb3React } from 'hooks'
import { useCurrency } from 'hooks/Tokens'
import useWrapCallback, { WrapType } from 'hooks/useWrapCallback'
import { Field } from 'state/swap/actions'
import { tryParseAmount, useDefaultsFromURL, useDerivedSwapInfo, useSwapActionHandlers, useSwapState } from 'state/swap/hooks'
import { useExpertModeManager, useUserSlippageTolerance } from 'state/user/hooks'
import { maxAmountSpend } from 'utils/maxAmountSpend'
import Loader from 'components/Loader'
import useI18n from 'hooks/useI18n'
import PageHeader from 'components/PageHeader'
import ConnectWalletButton from 'components/ConnectWalletButton'
import { useOneInchApprove, useOneInchSwap, useSendOneInchTxCallback } from 'hooks/oneInch'
import { formatUnits } from 'ethers/lib/utils'
import { BigNumber } from 'ethers'
import { OpenCurrencyAmount } from 'entities/Order'
import { roundDecimals } from 'utils/prices'
import defaultTokenJson from '../../constants/token/pancakeswap.json'

export default function MarketTradeBox() {
  const loadedUrlParams = useDefaultsFromURL()
  const TranslateString = useI18n()
  const [inputValue, setInputValue] = useState('1')

  // token warning stuff
  const [loadedInputCurrency, loadedOutputCurrency] = [
    useCurrency(loadedUrlParams?.inputCurrencyId),
    useCurrency(loadedUrlParams?.outputCurrencyId),
  ]
  const [dismissTokenWarning, setDismissTokenWarning] = useState<boolean>(false)
  const [transactionWarning, setTransactionWarning] = useState<{
    selectedToken: string | null
    purchaseType: string | null
  }>({
    selectedToken: null,
    purchaseType: null,
  })
  const urlLoadedTokens: Token[] = useMemo(
    () => [loadedInputCurrency, loadedOutputCurrency]?.filter((c): c is Token => c instanceof Token) ?? [],
    [loadedInputCurrency, loadedOutputCurrency]
  )
  const unknownTokens = urlLoadedTokens
    .filter(token => !defaultTokenJson.tokens.some(known => known.address.toLowerCase() === token.address.toLowerCase()))
  const inputTokenLoaded = !loadedUrlParams?.inputCurrencyId || loadedUrlParams?.inputCurrencyId === 'BNB' || !!loadedInputCurrency
  const outputTokenLoaded = !loadedUrlParams?.outputCurrencyId || loadedUrlParams?.outputCurrencyId === 'BNB' || !!loadedOutputCurrency

  const handleConfirmTokenWarning = useCallback(() => {
    setDismissTokenWarning(true)
  }, [])
  const handleConfirmWarning = () => {
    setTransactionWarning({
      selectedToken: null,
      purchaseType: null,
    })
  }

  const { account } = useActiveWeb3React()
  const theme = useContext(ThemeContext)

  const [isExpertMode] = useExpertModeManager()

  // get custom setting values for user
  // const [deadline] = useUserDeadline()
  const [allowedSlippage] = useUserSlippageTolerance()

  // swap state
  const { independentField, independentValue, recipient } = useSwapState()
  const { priceTrade, currencyBalances, currencies } = useDerivedSwapInfo()
  const v2Trade = priceTrade ?? undefined

  const baseCurrency = currencies[Field.INPUT]
  const quoteCurrency = currencies[Field.OUTPUT]
  const parsedInputAmount = tryParseAmount(inputValue, baseCurrency)

  const { wrapType, execute: onWrap, inputError: wrapInputError } = useWrapCallback(
    baseCurrency,
    quoteCurrency,
    independentValue
  )
  const showWrap: boolean = wrapType !== WrapType.NOT_APPLICABLE
  const pancakeSwapTrade = showWrap ? undefined : v2Trade

  const { onSwitchTokens, onCurrencySelection } = useSwapActionHandlers()

  const {
    data: quoteData,
    needsAllowance,
    loading: quoteLoading,
    error: quoteError,
    refetch: quoteRefetch,
  } = useOneInchSwap(
    baseCurrency && quoteCurrency && parsedInputAmount && account && allowedSlippage ? {
      baseCurrency,
      quoteCurrency,
      amount: parsedInputAmount,
      fromAddress: account,
      slippage: allowedSlippage / 100
    } : undefined
  )
  const { sendTransactionCallback, loading: swapLoading, error: swapError, txHash } = useSendOneInchTxCallback()
  const { approveCallback, data: approvalData, loading: approvalLoading, error: approvalError } = useOneInchApprove()
  const approvalSubmitted = !!approvalData

  const swapPrice = baseCurrency && quoteCurrency && parsedInputAmount && quoteData?.toTokenAmount
    && !quoteLoading && new Price(
        baseCurrency,
        quoteCurrency,
        parsedInputAmount.raw,
        quoteData.toTokenAmount
      )
  const isValid = inputValue && !quoteError && quoteData

  const outputAmount = quoteCurrency && quoteData?.toTokenAmount
    ? new OpenCurrencyAmount(quoteCurrency, quoteData.toTokenAmount)
    : undefined
  const formattedOutput = quoteData?.toTokenAmount
    ? Number(formatUnits(BigNumber.from(quoteData.toTokenAmount), quoteData.toToken?.decimals || 18)).toString()
    : ''

  // modal and loading
  const [{ showConfirm, swapErrorMessage, attemptingTxn }, setSwapState] = useState<{
    showConfirm: boolean
    attemptingTxn: boolean
    swapErrorMessage: string | undefined
  }>({
    showConfirm: false,
    attemptingTxn: false,
    swapErrorMessage: undefined,
  })

  const maxAmountInput: CurrencyAmount | undefined = maxAmountSpend(currencyBalances[Field.INPUT])
  const atMaxAmountInput = maxAmountInput && parsedInputAmount?.equalTo(maxAmountInput)

  const handleSwap = useCallback(async () => {
    if (!quoteData?.toTokenAmount) {
      return
    }

    const newQuote = await quoteRefetch()
    const diff = Number(newQuote?.toTokenAmount ?? 0) / Number(quoteData.toTokenAmount) - 1

    if (diff * 100 + allowedSlippage / 100 < 0) {
      setSwapState((prevState) => ({
        ...prevState,
        showConfirm: true,
        attemptingTxn: false,
        swapErrorMessage: undefined
      }))
      return
    }

    const swap = newQuote ?? quoteData

    const inputSymbol = swap.fromToken.symbol
    const outputSymbol = swap.toToken.symbol
    const input = roundDecimals(Number(swap.fromTokenAmount) / (10 ** (swap.fromToken.decimals ?? 18)), 6)
    const output = roundDecimals(Number(swap.toTokenAmount) / (10 ** (swap.toToken.decimals ?? 18)), 6)
    const txSummary = `Swap ${input} ${inputSymbol} for ${output} ${outputSymbol}`

    sendTransactionCallback(swap.tx, txSummary)

    setSwapState((prevState) => ({
      ...prevState,
      attemptingTxn: true,
      swapErrorMessage: undefined
    }))
  }, [quoteData, quoteRefetch, allowedSlippage, sendTransactionCallback])

  const handleApprove = useCallback(async () => {
    if (!baseCurrency) {
      return
    }

    const txData = await approveCallback(baseCurrency)
    if (txData) {
      await sendTransactionCallback(txData, `Approve ${baseCurrency.symbol}`)
    }
  }, [approveCallback, sendTransactionCallback, baseCurrency])

  // errors
  const [showInverted, setShowInverted] = useState<boolean>(false)

  const pancakeSwapDifference = pancakeSwapTrade?.executionPrice && swapPrice
    ? (Number(swapPrice.toSignificant(6)) / Number(pancakeSwapTrade.executionPrice.toSignificant(6))) - 1
    : 0

  // warnings on slippage
  const priceImpactSeverity = 0// warningSeverity(priceImpactWithoutFee)

  // show approve flow when: no error on inputs, not approved or pending, or approved in current session
  // never show if price impact is above threshold in non expert mode
  // const showApproveFlow =
  //   !swapInputError &&
  //   (approval === ApprovalState.NOT_APPROVED ||
  //     approval === ApprovalState.PENDING ||
  //     (approvalSubmitted && approval === ApprovalState.APPROVED)) &&
  //   !(priceImpactSeverity > 3 && !isExpertMode)
  const showApproveFlow = needsAllowance

  const handleConfirmDismiss = useCallback(() => {
    setSwapState((prevState) => ({ ...prevState, showConfirm: false }))

    // if there was a tx hash, we want to clear the input
    if (txHash) {
      setInputValue('')
    }
  }, [txHash])

  // This will check to see if the user has selected Syrup or SafeMoon to either buy or sell.
  // If so, they will be alerted with a warning message.
  const checkForWarning = useCallback(
    (selected: string, purchaseType: string) => {
      if (['SYRUP', 'SAFEMOON'].includes(selected)) {
        setTransactionWarning({
          selectedToken: selected,
          purchaseType,
        })
      }
    },
    [setTransactionWarning]
  )

  const handleInputSelect = useCallback(
    (inputCurrency) => {
      onCurrencySelection(Field.INPUT, inputCurrency)
      if (inputCurrency.symbol === 'SYRUP') {
        checkForWarning(inputCurrency.symbol, 'Selling')
      }
      if (inputCurrency.symbol === 'SAFEMOON') {
        checkForWarning(inputCurrency.symbol, 'Selling')
      }
    },
    [onCurrencySelection, checkForWarning]
  )

  const handleMaxInput = useCallback(() => {
    if (maxAmountInput) {
      setInputValue(maxAmountInput.toExact())
    }
  }, [maxAmountInput])

  const handleOutputSelect = useCallback(
    (outputCurrency) => {
      onCurrencySelection(Field.OUTPUT, outputCurrency)
      if (outputCurrency.symbol === 'SYRUP') {
        checkForWarning(outputCurrency.symbol, 'Buying')
      }
      if (outputCurrency.symbol === 'SAFEMOON') {
        checkForWarning(outputCurrency.symbol, 'Buying')
      }
    },
    [onCurrencySelection, checkForWarning]
  )

  const SwapButton = (props: ButtonProps) => (
    <Button
      onClick={handleSwap}
      id="swap-button"
      disabled={quoteLoading || swapLoading || !!quoteError}
      variant={(isValid && priceImpactSeverity > 2) || quoteError ? 'danger' : 'primary'}
      { ...props }
    >
      { quoteError || 'Swap' }
      { swapLoading && <Spinner animation="border" size="sm" variant="dark" /> }
    </Button>
  )

  return (
    <>
     <TokenWarningModal
        isOpen={unknownTokens.length > 0 && !dismissTokenWarning}
        tokens={unknownTokens}
        onConfirm={handleConfirmTokenWarning}
      />
      <SyrupWarningModal
        isOpen={transactionWarning.selectedToken === 'SYRUP'}
        transactionType={transactionWarning.purchaseType}
        onConfirm={handleConfirmWarning}
      />
      <SafeMoonWarningModal isOpen={transactionWarning.selectedToken === 'SAFEMOON'} onConfirm={handleConfirmWarning} />
      <ConfirmSwapModal
        isOpen={showConfirm}
        showPriceUpdated
        inputAmount={parsedInputAmount}
        outputAmount={outputAmount}
        trade={pancakeSwapTrade}
        attemptingTxn={attemptingTxn}
        txHash={txHash}
        recipient={recipient}
        allowedSlippage={allowedSlippage}
        onConfirm={handleSwap}
        swapErrorMessage={swapErrorMessage}
        onDismiss={handleConfirmDismiss}
      />

      <Wrapper id="swap-page">
        <PageHeader
          title="Swap at Market Price"
          description="Swap tokens at the best available price."
        />
        <CardBody>
          <AutoColumn gap="md">
            <CurrencyInputPanel
              label={
                independentField === Field.OUTPUT && !showWrap && pancakeSwapTrade
                  ? 'You Pay (estimated)'
                  : 'You Pay'
              }
              value={inputValue}
              showMaxButton={!atMaxAmountInput}
              currency={currencies[Field.INPUT]}
              currencyLoading={!inputTokenLoaded}
              onUserInput={setInputValue}
              onMax={handleMaxInput}
              onCurrencySelect={handleInputSelect}
              otherCurrency={currencies[Field.OUTPUT]}
              id="swap-currency-input"
            />
            
            <AutoColumn justify="space-between">
              <AutoRow justify={isExpertMode ? 'space-between' : 'center'} style={{ padding: '0 1rem' }}>
                <ArrowWrapper clickable>
                  <IconButton
                    variant="tertiary"
                    onClick={onSwitchTokens}
                    style={{ borderRadius: '50%' }}
                    scale="md"
                  >
                    <IoSwapVertical size={25} />
                  </IconButton>
                </ArrowWrapper>
              </AutoRow>
            </AutoColumn>

            <CurrencyInputPanel
              value={formattedOutput}
              loading={quoteLoading}
              disabled
              onUserInput={() => undefined}
              label="You Receice (estimated)"
              showMaxButton={false}
              currency={currencies[Field.OUTPUT]}
              currencyLoading={!outputTokenLoaded}
              onCurrencySelect={handleOutputSelect}
              otherCurrency={currencies[Field.INPUT]}
              id="swap-currency-output"
            />

            {showWrap ? null : (
              <Card padding=".25rem .75rem 0 .75rem" borderRadius="20px">
                <AutoColumn gap="4px">
                  {!!swapPrice && (
                    <RowBetween align="center">
                      <Text fontSize="14px">Price</Text>
                      <TradePrice
                        price={swapPrice}
                        showInverted={showInverted}
                        setShowInverted={setShowInverted}
                      />
                    </RowBetween>
                  )}
                  {allowedSlippage !== INITIAL_ALLOWED_SLIPPAGE && (
                    <RowBetween align="center">
                      <Text fontSize="14px">{TranslateString(88, 'Slippage Tolerance')}</Text>
                      <Text fontSize="14px">{allowedSlippage / 100}%</Text>
                    </RowBetween>
                  )}
                </AutoColumn>
              </Card>
            )}
          </AutoColumn>
          <BottomGrouping>
            {!account ? (
              <ConnectWalletButton block />
            ) : showWrap ? (
              <Button disabled={Boolean(wrapInputError)} onClick={onWrap} block>
                {wrapInputError ??
                  (wrapType === WrapType.WRAP ? 'Wrap' : wrapType === WrapType.UNWRAP ? 'Unwrap' : null)}
              </Button>
            ) : needsAllowance ? (
              <RowBetween>
                <Button
                  onClick={handleApprove}
                  disabled={approvalLoading}
                  style={{ width: '48%' }}
                  variant={!needsAllowance ? 'success' : 'primary'}
                >
                  {approvalLoading ? (
                    <AutoRow gap="6px" justify="center">
                      Approving <Loader stroke="white" />
                    </AutoRow>
                  ) : approvalSubmitted && !needsAllowance ? (
                    'Approved'
                  ) : (
                    `Approve ${currencies[Field.INPUT]?.symbol}`
                  )}
                </Button>
                <SwapButton style={{ width: '48%' }} disabled={needsAllowance} />
              </RowBetween>
            ) : (
              <SwapButton block />
            )}
            {showApproveFlow && <ProgressSteps steps={[!needsAllowance]} />}
            { (swapError ?? approvalError) && <SwapCallbackError error={swapError ?? approvalError} /> }
          </BottomGrouping>

          {
            pancakeSwapDifference > 0 && (
              <Text fontSize="16px"
                    style={{ justifyContent: 'center', alignItems: 'center', display: 'flex', marginTop: 8 }}>
                Get&nbsp;
                <strong style={{ color: theme.colors.success }}>
                  {roundDecimals(pancakeSwapDifference * 100, 2)}%</strong>
                &nbsp;more than on PancakeSwap
              </Text>
            )
          }

        </CardBody>
      </Wrapper>
    </>
  )
}
