import React from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import LimitTradeBox from 'components/LimitTradeBox'
import OrderBook from 'components/OrderBook'
import { useDerivedSwapInfo } from 'state/swap/hooks'
import { Field } from 'state/swap/actions'
import AppBody from 'pages/AppBody'
import { Tab, Tabs } from 'react-bootstrap'
import MarketTradeBox from 'components/MarketTradeBox'

export default function TradePage () {
  const { currencies } = useDerivedSwapInfo()

  return (
    <Container fluid>
      <Row>
        <Col>
          <OrderBook baseCurrency={currencies[Field.INPUT]} quoteCurrency={currencies[Field.OUTPUT]} />
        </Col>
        <Col>
          <OrderBook baseCurrency={currencies[Field.OUTPUT]} quoteCurrency={currencies[Field.INPUT]} />
        </Col>
        <Col>
          <AppBody>
            <Tabs variant="pills" defaultActiveKey="limit" id="trade-box" style={{ padding: '16px 0 0 16px' }}>
              <Tab eventKey="limit" title="Limit">
                <LimitTradeBox />
              </Tab>
              <Tab eventKey="market" title="Market">
                <MarketTradeBox />
              </Tab>
            </Tabs>
          </AppBody>
        </Col>
      </Row>
    </Container>
  )
}
